@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:300,600,800&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Nunito Sans', sans-serif;
  background-color: hsl(0, 0%, 98%);
}

nav {
  height: 20%;
  background-color: hsl(0, 0%, 100%);
  box-shadow: 0px 2px 10px 3px hsla(0, 0%, 95%, 0.918);
}

.container {
  max-width: 90%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

p {
  font-weight: 600;
}

.input-container {
  max-width: 90%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  margin-top: 40px;
  position: relative;
  margin-bottom: 50px;
}

input {
  font-family: 'Nunito Sans', sans-serif;
  outline: none;
  border: none;
  font-weight: 600;
  border-radius: 5px;
  box-shadow: 0px 0px 10px 5px hsl(0, 0%, 95%);
  flex-basis: 30%;
  color: hsl(0, 0%, 70%);
  padding: 15px;
  text-indent: 50px;
}

input::-webkit-input-placeholder {
  color: hsl(0, 0%, 70%);
}

input::-moz-placeholder {
  color: hsl(0, 0%, 70%);
}

input:-ms-input-placeholder {
  color: hsl(0, 0%, 70%);
}

input::-ms-input-placeholder {
  color: hsl(0, 0%, 70%);
}

input::placeholder {
  color: hsl(0, 0%, 70%);
}

.fa-search {
  position: absolute;
  left: 27px;
  color: hsl(0, 0%, 60%);
}

.select {
  width: 22%;
  font-family: 'Nunito Sans', sans-serif;
  outline: none;
  border: none;
  font-weight: 600;
  border-radius: 5px;
  box-shadow: 0px 0px 10px 5px hsl(0, 0%, 95%);
  color: hsl(0, 0%, 52%);
}

.grid {
  display: flex;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  flex-wrap: wrap;
  max-width: 90%;
  margin: auto;
}

.country-card {
  background-color: white;
  box-shadow: 0px 0px 10px 5px hsl(0, 0%, 95%);
  padding-bottom: 10px;
  border-radius: 10px;
  font-size: 14px;
  flex-basis: 250px;
  margin-bottom: 30px;
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}

.country-card:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  cursor: pointer;
}

li {
  margin-bottom: 5px;
}

img {
  width: 100%;
  height: 160px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

ul {
  list-style: none;
  padding: 0;
}

.info {
  margin: 20px;
}

.link {
  text-decoration: none;
  color: black;
}

.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
  position: absolute;
  top: 25%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #fff;
  border-color: black transparent black transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}
@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.btn {
	border: none;
	outline: none;
	background-color: white;
	font-family: 'Nunito Sans', sans-serif;
	font-weight: 300; 
 	box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.3);
  	border-radius: 5px;
  /*	padding: 8px 25px 8px 10px;*/
  	width: 110px;
  	height: 35px;
  	padding-right: 20px;
  	-webkit-transition: -webkit-transform .2s;
  	transition: -webkit-transform .2s;
  	transition: transform .2s;
  	transition: transform .2s, -webkit-transform .2s;
}

.btn i {
	padding: 0 10px;
}

.btn:hover{
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
	cursor: pointer;
}

.detailsContainer {
	display: grid;
    max-width: 90%;
    margin: auto;
    grid-template-rows: 1fr 2fr;
    align-items: center;
}

.info-main img {
	width: 500px;
	height: auto;
	margin: 0;
	padding: 0;
}

.left li {
	margin-bottom: 10px;
}

.right li {
	margin-bottom: 10px;
}

span {
	font-weight: 600;
}


.info-main {
	display: flex;
	margin: 0;
	justify-content: space-between;
	align-items: center;
}

.info-right {
	flex-basis: 50%;
}

.left {
	float: left;
}

.right {
	float: right;
}



